<template>
  <div id="app">
    <router-view />
    <!-- 回到顶部 -->
    <BackTop></BackTop>
  </div>
</template>
<script>
export default {
  methods: {
    //请求user接口获取数据
    getUserData() {
      //请求user接口
      this.$axios({
        url: process.env.API_HOST + "/user",
      }).then((res) => {
        let userData = res.data[0];
        this.$store.commit("setUserData", userData);
      });
    },
    //获取博客详情内容
    getDescData() {
      //请求博客详细信息
      this.$axios({
        url: process.env.API_HOST + "/blog",
      })
        .then((res) => {
          this.$store.commit("setAllBlog", res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    if (this.$store.getters.getUserData == "") {
      this.getUserData();
    }
    if (this.$store.getters.getAllBlog == "") {
      this.getDescData();
    }
  },
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-track {
  background: white;

  border-radius: 2px;
}

body::-webkit-scrollbar-thumb {
  background: blueviolet;

  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background: white;
}

body::-webkit-scrollbar-corner {
  background: blueviolet;
}
</style>



