import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    //所有的数据都放在state中
    state: {
        allBlog: "",//存储所有博客信息 
        userData: "",//存储user接口信息
    },

    //操作数据，唯一的通道是mutations
    mutations: {
        //把博客信息存入
        setAllBlog(state, data) {
            state.allBlog = data
        },
        //存储user接口信息
        setUserData(state, data) {
            state.userData = data
        },
    },
    //获取数据
    getters: {
        //获取博客信息
        getAllBlog(state) {
            return state.allBlog
        },
        //获取user接口数据
        getUserData(state) {
            return state.userData
        },
    },
    //actions,可以来做异步操作，然后提交给mutations，而后再对state(数据)进行操作
    actions: {}
})


